.Home {
  font-size: 12pt;
  height: 100vh;
  z-index: 3;

  &__wrapper {
    font-size: 13pt; // modify to scale for screen sizes
    position: relative;
    top: 47%;
    transform: translateY(-50%);
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
  }

  &__art {
    position: relative;
    width: 10em;
    height: 9.5em;
  }

  &__glow {
    width: 15.8em;
    position: absolute;
    // filter: saturate(120%);

    &--back {
      top: -1.8em;
      left: -2em;
    }

    &--front {
      top: 1.9em;
      left: -2.7em;
    }
  }

  &__abstract {
    width: 12em;
    position: absolute;
    // filter: saturate(200%);

    &--back {
      top: 0em;
      left: -0.4em;
    }

    &--front {
      top: 3.5em;
      left: -1.1em;
    }

    &--path {
      stroke-dasharray: 239 239;
      stroke-dashoffset: 0;
    }
  }

  &__zak {
    width: 9.5em;
    position: absolute;
    left: 0.65em;
    top: 2.8em;
    // transition: 0.3s;
  }

  &__headline {
    width: 14em;
    stroke-dasharray: 130 130;
    stroke-dashoffset: 0;
  }

  &__navigation {
    overflow-x: hidden;
    position: absolute;
    bottom: 14%;
    right: 0;
    display: flex;
    flex-flow: column nowrap;
  }
  
  &__NavLink {
    text-align: right;
    margin: 0.5em 0;
    align-self: flex-end;
    filter: opacity(1);
    pointer-events: auto;
  }

  &__scrollMsg {
    font-size: 11pt;
    width: 100%;
    position: absolute;
    bottom: 10%;
    text-align: center;
    font-style: italic;
    color: rgb(97, 97, 97);
  }
}
